import { customElement, property } from 'lit/decorators.js';
import { css, CSSResultGroup, html, LitElement, TemplateResult } from 'lit';
import './zwc-avatar';
import '../zwc-badge/zwc-badge';

@customElement('zwc-avatar-status')
export class ZwcAvatarStatus extends LitElement {
  @property({ type: String, attribute: 'background-color', reflect: true })
  backgroundColor = '';

  @property({ type: String })
  color = '';

  @property({ type: String })
  size = 'md';

  @property({ type: Boolean })
  outline = false;

  static override styles?: CSSResultGroup | undefined = [
    css`
      *,
      *::before,
      *::after {
        box-sizing: border-box;
      }

      :host {
        height: fit-content;
        width: fit-content;
        display: block;
      }

      .avatar-status {
        position: relative;
        cursor: pointer;
        display: inline-flex;
        text-decoration: none;
      }

      .badge {
        --zwc-badge-border: var(--c-white);
        display: flex;
        position: absolute;
        right: 0px;
      }

      .badge-top {
        top: 0px;
      }

      .badge-bottom {
        bottom: 0px;
      }

      .avatar-status .avatar-basic {
        background-color: var(--c-zurichblue);
        color: white;
      }
    `,
  ];

  protected override render(): TemplateResult {
    return html`
      <div class="avatar-status">
        <zwc-avatar
          size=${this.size}
          background-color="${this.backgroundColor}"
          color="${this.color}"
          outline="${this.outline}"
        >
          <slot name="icon"></slot>
          <slot name="image"></slot>
          <slot></slot>
        </zwc-avatar>
        <div class="badge badge-top">
          <slot name="badge-top"></slot>
        </div>
        <div class="badge badge-bottom">
          <slot name="badge-bottom"></slot>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-avatar-status': ZwcAvatarStatus;
  }
}
